import { CREATE_SET } from '../actions'

const INITIAL = [
  'Bnkpress',
  'Chins',
  'Dips',
  'Facepulls',
  'Flyes',
  'French press',
  'Hantellyft t sidan',
  'Knbj',
  'Latsdrag',
  'Liggande tricepsextension',
  'Marklyft',
  'Raka marklyft',
  'Rumnska marklyft',
  'Pausknbj',
  'Press ver huvudet',
  'Skivstngsrodd',
  'Smalbnk',
  'Slrodd',
  'Upptlutad hantelpress',
  'Utfall',
]

const saveNewExercise = (exercises, {exercise: value}) => {
  if (exercises.map(s => s.toLowerCase()).indexOf(value) === -1) {
    exercises = [
      ...exercises, 
      value,
    ]
  }
  return exercises
}

export default (currentState = INITIAL, action) => {
  switch (action.type) {
    case CREATE_SET:
      return saveNewExercise(currentState, action.payload.draft)
    default:
      return currentState
  }
}